import React from 'react';

class AudioPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isPlaying: true
    };
  }

  handlePlay = () => {
    this.setState({
      isPlaying: false
    });
  }
  
  render() {
    let imgSource = this.isPlaying ? "../../../static/images/pause.svg" : "../../../static/images/play.svg";
    return (
      <div>
          <audio src="../static/audio/linkinpark.mp3" controls autoPlay hidden/>
          <button onClick={this.handlePlay}><img src={imgSource}/></button>
      </div>
    );
  }
}

export default AudioPlayer;